<template>
  <!-- 注册页面 -->
  <div class="register-body">
    <div class="register-box">
      <div class="register-header">
        <h3>注册蓝天优购供货商（工厂)</h3>
        <button @click="onclicklogin">登录</button>
      </div>
      <div class="register-section">
        <a-form
          id="components-form-demo-normal-login"
          :form="form"
          class="login-form"
          @submit="handleSubmit"
        >
          <a-form-item>
            <a-input
              v-decorator="[
                'userName',
                {
                  rules: [
                    {
                      required: true,
                      pattern: /^(13[0-9]|14[01456879]|15[0-35-9]|16[2567]|17[0-8]|18[0-9]|19[0-35-9])\d{8}$/,
                      message: '手机号为空或手机号格式不对',
                    },
                  ],
                },
              ]"
              placeholder="请输入手机号"
              @blur="shojihao"
            >
            </a-input>
          </a-form-item>
          <a-form-item>
            <a-input
              v-decorator="[
                'password',
                {
                  rules: [
                    {
                      required: true,
                      pattern: /^(?=.*([a-zA-Z].*))(?=.*[0-9].*)[a-zA-Z0-9-*/+.~!@#$%^&*()]{8,20}/,
                      message:
                        '密码不能为空密码格式首位必须是字母8到20位字母与数字',
                    },
                  ],
                },
              ]"
              type="password"
              placeholder="请输入密码"
            >
            </a-input>
          </a-form-item>
          <a-form-item>
            <a-input
              v-decorator="[
                'passwor',
                {
                  rules: [{ required: true, message: '确认密码不能为空' }],
                },
              ]"
              type="password"
              placeholder="请确认密码"
            >
            </a-input>
          </a-form-item>
          <a-form-item>
            <a-row :gutter="8">
              <a-col :span="14">
                <a-input
                  v-decorator="[
                    'captcha',
                    {
                      rules: [
                        {
                          required: true,
                          message: '验证码不能为空',
                        },
                      ],
                    },
                  ]"
                  placeholder="请输入验证码"
                />
              </a-col>
              <a-col :span="10">
                <a-button v-show="show" @click="getCode">获取验证码</a-button>
                <a-button v-show="!show">{{ count }} 秒</a-button>
              </a-col>
            </a-row>
          </a-form-item>
          <a-form-item>
            <a-checkbox
              v-decorator="[
                'remember',
                {
                  valuePropName: 'checked',
                  initialValue: true,
                },
              ]"
            >
              我已经阅读并同意《用户协议》《隐私协议》
            </a-checkbox>
            <a-button
              type="primary"
              html-type="submit"
              class="login-form-button"
            >
              立即注册
            </a-button>
          </a-form-item>
        </a-form>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  beforeCreate() {
    this.form = this.$form.createForm(this, { name: "normal_login" });
  },
  data() {
    return {
      show: true,
      count: "",
      timer: null,
    };
  },
  methods: {
    getCode() {
      this.form.validateFieldsAndScroll((err, values) => {
        let str = {
          mobile: values.userName,
          receiverType: "mobile",
          templateId: 2,
          scene: "",
        };

        this.$http.send(str).then((res) => {
          if (res.data.code == "0000") {
            const TIME_COUNT = 60;
            if (!this.timer) {
              this.count = TIME_COUNT;
              this.show = false;
              this.timer = setInterval(() => {
                if (this.count > 0 && this.count <= TIME_COUNT) {
                  this.count--;
                } else {
                  this.show = true;
                  clearInterval(this.timer);
                  this.timer = null;
                }
              }, 1000);
            }
          }
        });
      });
    },
    handleSubmit(e) {
      e.preventDefault();
      this.form.validateFieldsAndScroll((err, values) => {
        if (values.password != values.passwor) {
          return this.$message("两次密码不一样");
        }
        if (values.remember != true) {
          return this.$message("请先同意协议须知");
        }
        let str = {
          phone: values.userName,
          password: this.$md5(values.password),
          captcha: values.captcha,
        };
        this.$http.reg(str).then((res) => {
          console.log(res.data);
          if (res.data.code == "0000") {
            this.$message({
              showClose: true,
              message: "注册成功即将跳转登录页面",
              type: "success",
            });
            this.$router.push({
              path: "/",
            });
          }
        });
      });
    },
    shojihao() {
      this.form.validateFieldsAndScroll(async (err, values) => {
        let str = {
          phone: values.userName,
        };
        let { data } = await this.$http.isph(str);
        if (data.flag == "Y") {
          this.$message({
            showClose: true,
            message: "手机号已注册可直接登录",
            type: "success",
          });
        }
      });
    },
    onclicklogin() {
      this.$router.push({
        path: "/",
      });
    },
  },
};
</script>

<style scoped>
#components-form-demo-normal-login .login-form {
  max-width: 300px;
}
#components-form-demo-normal-login .login-form-forgot {
  float: right;
}
#components-form-demo-normal-login .login-form-button {
  width: 100%;
}
.register-body {
  background-image: url("/bg.jpg");
  height: 100vh;
  display: flex;
  align-items: center;
}
.register-box {
  width: 410px;
  height: 390px;
  margin: 0 auto;
  background: #fff;
  padding: 10px 10px;
}
.register-header {
  width: 100%;
  display: flex;
  justify-content: space-between;
}
.register-section {
  width: 100%;
}
</style>